import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    users: localStorage.getItem("users")
      ? JSON.parse(localStorage.getItem("users"))
      : [],
    loggedUser: sessionStorage.getItem("loggedUser")
      ? JSON.parse(sessionStorage.getItem("loggedUser"))
      : "",
    activityType: [
      {
        id: "1",
        name: "Conferncia",
        points: 200
      },
      {
        id: "2",
        name: "Workshop",
        points: 200
      },
      {
        id: "3",
        name: "Concurso",
        points: 200
      },
      {
        id: "4",
        name: "Seminrio",
        points: 200
      },
      {
        id: "5",
        name: "Projeto Extracurricular",
        points: 200
      },
      {
        id: "6",
        name: "Visita a Empresa",
        points: 200
      }
    ],
    locals: ["ESMAD", "Online", "Outros"],
    activities: localStorage.getItem("activities")
      ? JSON.parse(localStorage.getItem("activities"))
      : [],
    quizzes: localStorage.getItem("quizzes")
      ? JSON.parse(localStorage.getItem("quizzes"))
      : [],
    comments: localStorage.getItem("comments")
      ? JSON.parse(localStorage.getItem("comments"))
      : [],
    enrollments: localStorage.getItem("enrollments")
      ? JSON.parse(localStorage.getItem("enrollments"))
      : [],
    concluded_quizzes: localStorage.getItem("concluded_quizzes")
      ? JSON.parse(localStorage.getItem("concluded_quizzes"))
      : []
  },
  getters: {
    getLoggedUser: state => state.loggedUser,
    isLoggedUser: state => (state.loggedUser == "" ? false : true),
    getActivityType: state => state.activityType,
    getActivities: state => state.activities,
    getEnrollments: state => state.enrollments,
    getConcldedQuizzes: state => state.concluded_quizzes,
    getLocals: state => state.locals,
    getInterests: state => state.interests,
    getLoggedUserType: state => state.loggedUser.type,
    getNextUserId: state => {
      return state.users.length == 0
        ? 1
        : state.users[state.users.length - 1].id + 1;
    },
    getNextActivityId: state => {
      return state.activities.length > 0
        ? state.activities[state.activities.length - 1].id + 1
        : 1;
    },
    getNextQuizId: state => {
      return state.quizzes.length > 0
        ? state.quizzes[state.quizzes.length - 1].id + 1
        : 1;
    },
    getActivityTypeById: state => id => {
      return state.activityType.find(type => type.id == id);
    },
    getNumUsers: state => {
      return state.users.length;
    },
    getNumActivities: state => {
      return state.activities.length;
    },
    getNumQuizzes: state => {
      return state.quizzes.length;
    },
    getActivitiesForSelect: state =>
      state.activityType.map(type => ({
        value: type.id,
        text: type.name
      }))
  },
  actions: {
    login(context, payload) {
      if (payload.email == "admin" && payload.password == "admin") {
        let user = { name: "admin", password: "admin", type: "admin" };
        context.commit("LOGIN", user);
        sessionStorage.setItem("loggedUser", JSON.stringify(user));
      } else {
        //verificar se user existe
        const user = context.state.users.find(
          user =>
            user.email === payload.email && user.password === payload.password
        );
        if (user != undefined && user.blocked != true) {
          //login com sucesso
          context.commit("LOGIN", user);
          sessionStorage.setItem("loggedUser", JSON.stringify(user));
        } else if (user != undefined && user.blocked == true) {
          //login sem sucesso
          throw Error("User Bloqueado!");
        } else {
          //login sem sucesso
          throw Error("Login invlido!");
        }
      }
    },
    logout(context) {
      context.commit("LOGOUT");
      sessionStorage.removeItem("loggedUser");
    },
    register(context, payload) {
      //verificar se user existe
      const user = context.state.users.find(
        user => user.email === payload.email
      );
      if (user == undefined && payload.password != payload.password2) {
        throw Error("As passwords no so iguais!");
      } else if (user == undefined) {
        context.commit("REGISTER", {
          id: payload.id,
          name: payload.name,
          email: payload.email,
          password: payload.password,
          course: payload.course,
          birthDate: payload.birthDate,
          photo: payload.photo,
          type: payload.type,
          profileType: payload.profileType,
          points: payload.points,
          interests: payload.interests,
          achievements: payload.achievements,
          certificates: payload.certificates,
          blocked: payload.blocked
        });
        localStorage.setItem("users", JSON.stringify(context.state.users));
      } else {
        throw Error("Email j registado!");
      }
    },
    submitActivity(context, payload) {
      const activity = context.state.activities.find(
        activity => activity.name === payload.name
      );
      if (activity == undefined) {
        context.commit("ACTIVITY", payload);
        localStorage.setItem(
          "activities",
          JSON.stringify(context.state.activities)
        );
      } else {
        throw Error("Atividade j inserida!");
      }
    },
    submitQuiz(context, payload) {
      const quiz = context.state.quizzes.find(
        quiz => quiz.theme === payload.theme
      );
      if (quiz == undefined) {
        context.commit("QUIZ", payload);
        localStorage.setItem("quizzes", JSON.stringify(context.state.quizzes));
      } else {
        throw Error("Quiz j inserida!");
      }
    },
    concludedQuiz(context, payload) {
      const concluded_quiz = context.state.concluded_quizzes.find(
        concluded_quiz =>
          concluded_quiz.idUser === payload.idUser &&
          concluded_quiz.idQuiz === payload.idQuiz 
      );
      if (concluded_quiz == undefined) {
        context.commit("CONCLUDED_QUIZZES", payload);
        localStorage.setItem(
          "concluded_quizzes",
          JSON.stringify(context.state.concluded_quizzes)
        );
        localStorage.setItem("users", JSON.stringify(context.state.users));
        
        
      }
    },
    submitEnrollment(context, payload) {
      const activity = context.state.activities.find(
        activity => activity.id === payload.idActivity
      );
      const enrollment = context.state.enrollments.find(
        enrollment =>
          enrollment.idUser === payload.idUser &&
          enrollment.idActivity === payload.idActivity
      );
      if (enrollment == undefined) {
        let num = 0;
        for (let i = 0; i < context.state.enrollments.length; i++) {
          if (context.state.enrollments[i].idActivity == activity.id) {
            num++;
          }
        }
        if (num < activity.numPeople - 1) {
          console.log(num);
          context.commit("ENROLLMENT", payload);
          localStorage.setItem(
            "enrollments",
            JSON.stringify(context.state.enrollments)
          );
        } else if (num == activity.numPeople - 1) {
          context.commit("LASTENROLLMENT", payload);
          localStorage.setItem(
            "enrollments",
            JSON.stringify(context.state.enrollments)
          );
          localStorage.setItem(
            "activities",
            JSON.stringify(context.state.activities)
          );
          
        }
      } else {
        throw Error("J est Inscrito na atividade!");
      }
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    cancelEnrollment(context, payload) {
      context.commit("CANCEL_ENROLLMENT", payload);
      localStorage.setItem(
        "enrollments",
        JSON.stringify(context.state.enrollments)
      );
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    concludeActivity(context, id) {
      context.commit("CONCLUDE_ACTIVITY", id);
      localStorage.setItem(
        "activities",
        JSON.stringify(context.state.activities)
      );
    },
    editProfile(context, payload) {
      context.commit("PROFILE", payload);
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    updateActivity(context, payload) {
      context.commit("UPDATE_ACTIVITY", payload);
      console.log(payload);
      localStorage.setItem(
        "activities",
        JSON.stringify(context.state.activities)
      );
    },
    updateUser(context, payload) {
      context.commit("UPDATE_USER", payload);
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    removeUser(context, id) {
      context.commit("REMOVE_USER", id);
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    promoteUser(context, id) {
      context.commit("PROMOTE_USER", id);
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    demoteUser(context, id) {
      context.commit("DEMOTE_USER", id);
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    blockUser(context, id) {
      context.commit("BLOCK_USER", id);
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    unblockUser(context, id) {
      context.commit("UNBLOCK_USER", id);
      localStorage.setItem("users", JSON.stringify(context.state.users));
    },
    removeActivity(context, id) {
      context.commit("REMOVE_ACTIVITY", id);
      localStorage.setItem(
        "activities",
        JSON.stringify(context.state.activities)
      );
    },
    removeQuiz(context, id) {
      context.commit("REMOVE_QUIZ", id);
      localStorage.setItem("quizzes", JSON.stringify(context.state.quizzes));
    },
  },
  mutations: {
    SET_ACTIVITIES(state, payload) {
      state.activities = payload.activities;
      localStorage.setItem("activities", JSON.stringify(state.activities));
    },
    SET_QUIZZES(state, payload) {
      state.quizzes = payload.quizzes;
      localStorage.setItem("quizzes", JSON.stringify(state.quizzes));
    },
    SET_USERS(state, payload) {
      state.users = payload.users;
      localStorage.setItem("users", JSON.stringify(state.users));
    },
    LOGIN(state, user) {
      state.loggedUser = user;
    },
    LOGOUT(state) {
      state.loggedUser = "";
    },
    REGISTER(state, user) {
      state.users.push(user);
    },
    ACTIVITY(state, activity) {
      state.activities.push(activity);
    },
    QUIZ(state, quiz) {
      state.quizzes.push(quiz);
    },
    CONCLUDED_QUIZZES(state, payload) {
      state.concluded_quizzes.push(payload);
      state.users.map(user => {
        if (user.id === payload.idUser) {
          user.points = user.points + 250
          sessionStorage.setItem("loggedUser", JSON.stringify(user));
        }})
       /*  state.loggedUser.points = state.loggedUser.points + 250 */
    },
    ENROLLMENT(state, payload) {
      state.enrollments.push(payload);
      state.users.map(user => {
        if (user.id == payload.idUser) {
            user.points = user.points + 200
            sessionStorage.setItem("loggedUser", JSON.stringify(user));
        }
    })
    },
    CANCEL_ENROLLMENT(state, payload) {
      state.enrollments = state.enrollments.filter(enrollment => {
        if (enrollment.idActivity == payload.idActivity) {
          if (enrollment.idUser == payload.idUser) {
            return false;
          } else {
            return true;
          }
        } else {
          return true;
        }
      });
      state.users.map(user => {
        if (user.id == payload.idUser) {
            user.points = user.points - 200
            sessionStorage.setItem("loggedUser", JSON.stringify(user));
        }
    })
    },
    LASTENROLLMENT(state, payload) {
      state.enrollments.push(payload);
      state.activities.map(activity => {
        if (activity.id === payload.id) {
          activity.full = true;
          console.log(payload.id);
          console.log(activity.id);
        }
      });
    },
    PROFILE(state, payload) {
      state.users.map(user => {
        if (user.id === state.loggedUser.id) {
          user.password = payload.frm.password;
          user.photo = payload.frm.photo;
          console.log(payload.frm.photo);
          user.interests = payload.frm.interests;
          sessionStorage.setItem("loggedUser", JSON.stringify(user));
        }
      });
      state.loggedUser.password = payload.frm.password;
      state.loggedUser.photo = payload.frm.photo;
      state.loggedUser.interests = payload.frm.interests;
    },
    CONCLUDE_ACTIVITY(state, id) {
      /* let date1 = new Date(); */
      state.activities.map(activity => {
        if (activity.id === id) {
          activity.concluded = true;
          /* if (date1.getTime() > activity.date) {
              activity.concluded = true
            }
            else {
              activity.concluded = false
              throw Error('No  possivel concluir a atividade!')
              
            } */
        }
      });
    },
    UPDATE_USER(state, payload) {
      state.users.map(user => {
        if (user.id === payload.id) {
          user.name = payload.name;
          user.email = payload.email;
          user.course = payload.course;
          user.birthDate = payload.birthDate;
        }
      });
    },
    UPDATE_ACTIVITY(state, payload) {
      state.activities.map(activity => {
        if (activity.id === payload.id) {
          activity.name = payload.name;
          activity.date = payload.date;
          activity.hour = payload.hour;
          activity.local = payload.local;
          activity.description = payload.description;
        }
      });
    },
    REMOVE_USER(state, id) {
      state.users = state.users.filter(user => user.id != id);
    },
    PROMOTE_USER(state, id) {
      state.users.map(user => {
        if (user.id === id) user.type = "Docente";
      });
    },
    DEMOTE_USER(state, id) {
      state.users.map(user => {
        if (user.id === id) user.type = "Estudante";
      });
    },
    BLOCK_USER(state, id) {
      state.users.map(user => {
        if (user.id === id) user.blocked = true;
      });
    },
    UNBLOCK_USER(state, id) {
      state.users.map(user => {
        if (user.id === id) user.blocked = false;
      });
    },
    REMOVE_ACTIVITY(state, id) {
      state.activities = state.activities.filter(activity => activity.id != id);
    },

  REMOVE_QUIZ(state, id) {
    state.quizzes = state.quizzes.filter(quiz => quiz.id != id);
  },
}
});
