<template>
  <div id="content">
    <b-container id="info">
      <b-card class="profileCard">
        <h1>Perfil</h1>
        <b-form @submit.prevent="editProfile">
          <b-row>
            <b-img
              id="foto"
              center
              :src="getUser.photo"
              rounded="circle"
              alt="Profile photo"
            ></b-img>
            <b-row>
              <b-input-group class="mb-3 mt-3">
                <b-form-input
                  type="url"
                  class="input"
                  v-model="frm.photo"
                  placeholder="Insira um novo link"
                />
              </b-input-group>
            </b-row>

            <b-row>
              <div id="name" class="col">
                <h5>Nome</h5>
                <p>{{ getUser.name }}</p>
              </div>
              <div id="birthDate" class="col">
                <h5>Data de nascimento</h5>
                <p>{{ getUser.birthDate }}</p>
              </div>
            </b-row>
            <b-row>
              <div id="email" class="col">
                <b-form-group>
                  <h5>E-mail</h5>
                  <p>{{ getUser.email }}</p>
                </b-form-group>
              </div>
              <div id="pass" class="col">
                <b-row>
                  <b-input-group class="mb-3">
                    <b-row>
                      <h5>Deseja alterar a password?</h5>
                    </b-row>
                    <b-row>
                      <b-form-group>
                        <b-form-input
                          type="password"
                          id="txtPassword"
                          placeholder="Password"
                          v-model="frm.password"
                        />
                      </b-form-group>
                    </b-row>
                  </b-input-group>
                </b-row>
              </div>
            </b-row>
            <b-row>
              <div id="course">
                <h5>Curso</h5>
                <p>{{ getUser.course }}</p>
              </div>
              <b-form>
                <b-row>
                  <h5>Interesses</h5>
                  <b-form-checkbox-group
                    id="checkboxes-4"
                    v-model="frm.interests"
                    :options="interests"
                  >
                  </b-form-checkbox-group>
                </b-row>
              </b-form>
            </b-row>
          </b-row>
          <b-button variant="secondary" type="submit" class="btn"
            >Atualizar</b-button
          >
        </b-form>
      </b-card>
      <b-card class="gamificationCard" id="gamification">
        <h1>GAMIFICAO</h1>
        <b-row>
          <h6>
            Tipo de perfil: <span>{{ getProfileType() }} </span>
            <b-button v-b-modal.modal-1 id="infobtn">
              <ion-icon name="information-circle-outline"></ion-icon>
            </b-button>
          </h6>
          <h6>
            Pontos: <span>{{ this.$store.getters.getLoggedUser.points }} </span
            ><button v-b-modal.modal-2 id="infobtn">
              <ion-icon name="information-circle-outline"></ion-icon>
            </button>
          </h6>
          <h6>
            Conquistas: <span>{{ getUser.achievements }} </span
            ><button v-b-modal.modal-3 id="infobtn">
              <ion-icon name="information-circle-outline"></ion-icon>
            </button>
          </h6>
          <h6>
            Certificados: <span>{{ getUser.certificates }} </span
            ><button v-b-modal.modal-4 id="infobtn">
              <ion-icon name="information-circle-outline"></ion-icon>
            </button>
          </h6>
        </b-row>
        <b-modal
          id="modal-1"
          header-text-variant="warning"
          centered
          title="TIPO DE PERFIL"
        >
          <h6 class="gray"><i class="far fa-gem"></i> Iniciante:</h6>
          <p>Utilizador com menos de 1000 pontos inclusive.</p>
          <h6 class="purple"><i class="far fa-gem"></i> Intermdio:</h6>
          <p>Utilizador com mais de 1000 pontos e menos de 3500 inclusive.</p>
          <h6 class="yellow"><i class="far fa-gem"></i> Avanado:</h6>
          <p>Utilizador com mais de 3500 pontos.</p>
        </b-modal>
        <b-modal
          id="modal-2"
          header-text-variant="warning"
          centered
          title="COMO GANHAR PONTOS? "
        >
          <p class="my-4">
            A cada atividade que participes ganhas X pontos. Participa em vrias
            para conseguires subir de nvel!
          </p>
        </b-modal>
        <b-modal id="modal-3" centered title="CONQUISTAS">
          <p class="my-4">No possui conquistas!</p>
        </b-modal>
        <b-modal id="modal-4" centered title="CERTIFICADOS">
          <p class="my-4">No possui certificados!</p>
        </b-modal>
      </b-card>
    </b-container>
  </div>
</template>

<script>
export default {
  name: "Profile",
  data() {
    return {
      interests: [
        "Animao",
        "Design",
        "Matemtica",
        "Multimdia",
        "Programao",
      ],
      frm: {
        password: "",
        photo: "",
        interests: [],
      },
      current_user: this.$store.getters.getLoggedUser,
    };
  },
  created() {
    this.frm.password = this.getUser.password;
    this.frm.photo = this.getUser.photo;
    this.frm.interests = this.getUser.interests;

    /*  this.quizzes= this.$store.getters.getConcldedQuizzes.filter( quiz => quiz.idUser == this.$store.getters.getLoggedUser); */

    /*  if(quiz.idUser == this.$store.getters.getLoggedUser){

        for( let i = 0 ; i < this.quizzes.lenght; i++ )
        {
          this.points += quiz.points
        } */
  },
  methods: {
    editProfile() {
      try {
        this.$store.dispatch("editProfile", this.$data);
        console.log(this.getUser.photo);
      } catch (error) {
        alert(error);
      }
    },
    addInterests() {
      try {
        this.$store.dispatch("addInterests", this.$data);
      } catch (error) {
        alert(error);
      }
    },

    getProfileType() {
      let type = "";

      if (this.current_user.points <= 1000) {
        type = "Iniciante";
      }

      else if (this.current_user.points > 1000 && this.current_user.points <= 3500) {
        type = "Intermdio";
      } else {
        type = "Avanado";
      }
      return type;
    },
  },
  computed: {
    getUser() {
      return this.$store.getters.getLoggedUser;
    },
  },
};
</script>

<style scoped>
#content {
  padding-top: 80px;
  background-color: #dcdcdc;
  min-height: 100vh;
}
.gamificationCard {
  margin-top: 20px;
}
#info h1,
#gamification h1 {
  font-size: 30px;
  font-weight: 700;
  margin-bottom: 25px;
  color: #fec107;
  text-transform: uppercase;
  text-align: center;
}
#info h5,
#info p,
#gamification h6 {
  text-align: left;
}
#foto {
  width: 30%;
}
#gamification span {
  font-size: 12px;
}
#infobtn {
  background: none;
  border: none;
}
#gamification ion-icon {
  color: #fec107;
  top: 4px;
  position: relative;
}
#gamification ion-icon:hover {
  color: #ffffff;
  top: 4px;
  position: relative;
  border-radius: 100%;
  background: #fec107;
}
h6.gray {
  color: #b8b9b9;
}
h6.purple {
  color: #3c3641;
}
h6.yellow {
  color: #f3b944;
}
</style>
