<template>
  <div id="Activity">
    <div class="container-xl activityCard">
      <div class="row">
        <div class="col-lg-4">
          <img class="img-fluid imgActivity mt-3" :src="activity.image" alt=""/>
          <div class="eventDate mt-3">
            <p>Data: {{ activity.date }}</p>
            <p>Participantes: {{ activity.numPeople }}</p>
            <p>Hora: {{ activity.hour }}</p>
          </div>
          <div v-if="getUser.type == 'Estudante'">
            <div v-if="subscribed == undefined">
              <b-button
                @click="newEnrollment(activity.id)"
                variant="outline-warning"
                class="btn"
              >
                Inscrever
              </b-button>
            </div>
            <div v-else>
              <b-button
                @click="removeEnrollment(activity.id)"
                variant="outline-warning"
                class="btn"
              >
                Cancelar Subscrio
              </b-button>
            </div>
          </div>
          <div v-else>
            <b-button
              class="mb-3"
              @click="removeActivity(activity.id)"
              variant="danger"
            >
              REMOVER
            </b-button>

            <b-button
              class="mb-3"
              @click="updateActivity(activity.id)"
              variant="info"
            >
              <ion-icon name="create-outline"></ion-icon> EDITAR
            </b-button>
            <div v-if="activity.concluded == false">
              <b-button
                class="mb-3"
                @click="concludeActivity(activity.id)"
                variant="success"
              >
                <ion-icon name="create-outline"></ion-icon> CONCLUIR
              </b-button>
            </div>
            <div v-else>
              <b-button
                class="mb-3"
                @click="concludeActivity(activity.id)"
                variant="success"
                disabled
              >
                <ion-icon name="create-outline"></ion-icon> CONCLUIR
              </b-button>
            </div>
          </div>
        </div>

        <div class="col-lg-8">
          <div class="text-left mt-4">
            <b-card title="Nome" class="cardDetails">
              <div>
                <p>
                  {{ activity.name }}
                </p>
                <h4>Tipo</h4>
                <p>
                  {{ activity.type }}
                </p>
                <h4>Localizao</h4>
                <p>
                  {{ activity.local }}
                </p>

                <h4>Certificado</h4>
                <p>
                  {{ activity.certificate }}
                </p>
                <h4>Descrio</h4>
                <p>
                  {{ activity.description }}
                </p>
              </div>
            </b-card>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Activity",
  data() {
    return {
      activity: null,
      enrollments: this.$store.state.enrollments,
      subscribed: undefined
    };
  },
  created() {
    let activities = [];
    if (localStorage.getItem("activities")) {
      activities = JSON.parse(localStorage.getItem("activities"));
    }
    this.activity = activities.find(g => g.id == this.$route.params.id);
    console.log(this.activity);
    this.subscribed = this.$store.state.enrollments.find(enrollment => {
      if (enrollment.idActivity == this.activity.id) {
        if (enrollment.idUser == this.$store.getters.getLoggedUser.id) {
          return true;
        } else {
          return false;
        }
      } else {
        return false;
      }
    });
  },
  methods: {
    goBack() {
      this.$router.push({ name: "ListActivity" });
    },
    removeActivity(id) {
      if (confirm("Tem a certeza que pretende remover?")) {
        this.$store.dispatch("removeActivity", id);
      }
    },
    updateActivity(id) {
      this.$router.push({ name: "UpdateActivity", params: { id: id } });
    },
    concludeActivity(id) {
      try {
        if (confirm("Tem a certeza que pretende concluir?")) {
          this.$store.dispatch("concludeActivity", id);
        }
      } catch (error) {
        alert(error);
      }
    },
    newEnrollment(id) {
      try {
        this.$store.dispatch("submitEnrollment", {
          idActivity: id,
          idUser: this.$store.getters.getLoggedUser.id
        });
        this.subscribed = "true";
      } catch (error) {
        alert(error);
      }
    },
    removeEnrollment(payload) {
      console.log("idUser:", this.$store.getters.getLoggedUser.id);
      console.log("idAtivity:", payload);

      try {
        this.$store.dispatch("cancelEnrollment", {
          idActivity: payload,
          idUser: this.$store.getters.getLoggedUser.id
        });
        this.subscribed = undefined;
      } catch (error) {
        alert(error);
      }
    }
  },

  computed: {
    getUser() {
      return this.$store.getters.getLoggedUser;
    }
  }
};
</script>

<style>
#activity p,
#activity h5 {
  text-align: left;
}
#activity {
  padding-top: 80px;
}
.activityCard {
  margin-top: 120px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 0 10px #81790f;
}
.imgActivity {
  width: 300px;
  height: 300px;
  border-radius: 10px;
}

.cardDetails {
  border: none !important;
}
</style>
